}

.movable-container {
  display: flex;
  gap: 8px;
  flex-direction: column;
  align-items: stretch;
  justify-content: stretch;
  z-index: 10000;
  position: absolute;
  width: 100%;
  height: 100%;
}

.is-dragged {
  overflow: hidden;
}
.is-dragged > * {
  width: 0 !important;
  min-width: 0 !important;
  height: 0 !important;
  min-height: 0 !important;
  padding: 0 !important;
  margin: 0 !important;
  overflow: hidden !important;
}

.movable-element {
  min-width: 0;
  min-height: 0;
  transition: min-width, min-height 0.2s ease;
  transition-delay: 0.1s;
}

.is-dragging #area-left, .is-dragging #area-right {
