}

.pin {
  filter: grayscale(1);
  display: inline-block;
  transition: rotate 0.1s ease-in, translate 0.12s;
  transition-delay: 0.07s, 0s;
}
button:has(.pin):not(:hover) {
  border-color: transparent;
}

.pinned-here .pin {
  filter: none;
  rotate: -45deg;
  translate: 0 9px;
  transition-delay: 0s, 0.07s;
}

.pinned-initial .pin {
  filter: none;
  rotate: 45deg;
  translate: -22px 2px;
  transition-delay: 0s, 0.07s;
}

.pin-latest .pin {
  rotate: -90deg;
}

.pinned-latest .pin {
  filter: none;
  rotate: -135deg;
  translate: 22px 3px;
  transition-delay: 0s, 0.07s;
}

button {
  width: auto;
  display: inline-block;
}

.svg-inspect-wrapper {
  overflow: hidden !important;
