  flex-direction: column !important;
}

@keyframes blink {
  0% {
    box-shadow: 0 0 0 0 rgba(124, 50, 177, 0.708);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(76, 0, 130, 0.406);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(76, 0, 130, 0.721);
  }
}
:has(> .tutorial-content) {
