}

.chrome-picker {
  user-select: none;
}
.chrome-picker * {
  transition-property: top, left;
  transition-duration: 0.12s, 0.12s;
  transition-timing-function: ease-out;
}

input[type=checkbox] + * {
  display: inline-block;
}

::-webkit-slider-thumb {
  appearance: auto !important;
}

.oklch-picker input[type=range] {
  min-height: 0;
  padding: 0;
  outline: none !important;
  --picked: oklch(var(--picked-lightness, 63%) var(--picked-chroma, 0.1) var(--picked-hue, 180));
  accent-color: grey;
}
.oklch-picker input[type=range]::-webkit-slider-thumb {
  accent-color: oklch(var(--picked-lightness, 63%) var(--picked-chroma, 0.1) var(--picked-hue, 180));
}
.oklch-picker input[type=range]#hue::-webkit-slider-runnable-track, .oklch-picker input[type=range]:focus::-webkit-slider-runnable-track, .oklch-picker input[type=range] .hovering-with-color::-webkit-slider-runnable-track {
  background: var(--picked);
  padding-top: 4px;
}

#hue,
#lightness,
#chroma,
#alpha {
  position: absolute;
  top: 41px;
  max-width: 352px;
  width: 352px;
  margin-left: -6px;
  box-sizing: border-box;
  user-select: none;
  margin-top: 0;
}

#hue:before,
#lightness:before,
#chroma:before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 100%;
  width: 100%;
  height: 36px;
  background: transparent;
}

#alpha {
  opacity: calc(0.5 + (1 - var(--picked-alpha, 1)) * 1000);
}
#alpha:focus {
  opacity: 1;
}
#alpha:focus::-webkit-slider-runnable-track {
  background: var(--picked);
  opacity: var(--picked-alpha);
}

.hue, .chroma, .lightness, .alpha {
  position: relative;
  height: 42px;
  width: 340px;
  margin: 20px 0;
